/* stylelint-disable */
@mixin font-face($font-family, $url, $weight, $style, $extras: ()) {
  @font-face {
    font-family: "#{$font-family}";
    src: url('../fonts/#{$url}.woff2') format("woff2");
    font-weight: #{$weight};
    font-display: swap;
    font-style: $style;
    // @each $key, $value in $extras {
    //   #{$key}: #{$value};
    // }
  }
}
